.photo {
  height: 80px;
  width: 100px;
}
.btn {
  background-color: #f1f1f1;
  border: 1px solid rgb(155, 155, 155);
  border-radius: 4px;
  cursor: pointer;
  -ms-flex-preferred-size: 60%;
  flex-basis: 50%;
  font-family: Sailec-Bold, system-ui, -apple-system, BlinkMacSystemFont,
    Segoe UI, Roboto, Helvetica Neue, Helvetica, Arial, sans-serif;
  font-size: 16px;
  padding: 12px 32px;
  text-align: center;
  position: sticky;
  display: inline-block;
  text-transform: uppercase;
  width: min-content;
  height: auto;
  float: left;
  line-height: 1.15;
  margin-bottom: 5%;
}
.btn:hover {
  box-shadow: 0 12px 16px 0 rgba(0, 0, 0, 0.24),
    0 17px 50px 0 rgba(0, 0, 0, 0.19);
}

.btn {
  transition-duration: 0.2s;
}

.btn:hover {
  background-color: #d6d2d2;
  color: rgb(8, 8, 8);
}

.btn_selected {
  background-color: #1093eb;
  border: 1px solid rgb(7, 73, 128);
  border-radius: 5px;
  color: #dee3ec;
  cursor: pointer;
  -ms-flex-preferred-size: 50%;
  flex-basis: 40%;
  font-family: Sailec-Bold, system-ui, -apple-system, BlinkMacSystemFont,
    Segoe UI, Roboto, Helvetica Neue, Helvetica, Arial, sans-serif;
  font-size: 16px;
  padding: 15px 32px;
  text-transform: uppercase;
  text-align: center;
  position: relative;
  display: inline-block;
  text-transform: uppercase;
  width: auto;
  height: auto;
  float: left;
}

.btn_selected:hover {
  box-shadow: 0 12px 16px 0 rgba(0, 0, 0, 0.24),
    0 17px 50px 0 rgba(0, 0, 0, 0.19);
}
.sld {
  display: block;
  margin-top: 5%;
  margin-left: 1%;
  position: relative;
}

.rowC {
  display: block;
  flex-direction: block;
  margin-right: 70%;
  margin-bottom: 2%;
  max-width: 100%;
  font-size: 90%;
}

.leftside,
.rightside {
  height: 100vh;
  width: 100%;
}

.bord {
  border-style: groove;
  border-width: 10px;

  color: #343a40;
}
